<template>
  <div class="container">
     <div class="row mb-4 d-flex align-items-top">
        <div class="col-12 col-sm-3 mb-2 pt-4 mt-1" v-if="esAdmin">
          <b-button block variant="success" @click="$bvModal.show('Agregar')">Agregar Producto</b-button>    
        </div>
      </div>
    <b-table 
    responsive
    striped
    hover
    :items="Productos.items" 
    :fields="Productos.fields"
    head-variant="dark"
    >
    <template #cell(estatus)="data">
      <span :class="{'text-success': data.item.estatus === 1, 'text-danger': data.item.estatus === 0}">
        {{ data.item.estatus === 1 ? 'Activo' : 'Inactivo' }}
      </span>
    </template>
    <template v-if="esAdmin" v-slot:cell(actions)="data">
      <a v-b-tooltip.hover title="Editar Estatus" @click="abrirModalEstatus(data.item)">
        <b-icon icon="pencil-square" font-scale = "1.5"></b-icon>
      </a>
    </template>
    <template v-if="esAdmin" v-slot:cell(actions2)="data">
      <a v-b-tooltip.hover title="Entrada Producto" @click="abrirModalEntrada(data.item)">
        <b-icon icon="cart-plus-fill" font-scale = "1.5"></b-icon>
      </a>
    </template>
    </b-table>

    <!--Modal RegistroNuevoProducto-->
    <div>
      <b-modal
      no-enforce-focus
      no-close-on-backdrop 
      no-close-on-esc 
      scrollable 
      hide-footer 
      size="xl" 
      centered 
      ignore-enforce-focus-selector=".swal2-popup" 
      id="Agregar" 
      title="Agregar Producto"
      >
        <b-form @submit="onSubmitRegistro" @reset="onResetRegistro">
          <div class="row">
            <div class="col-6 col-md-3">
              <b-form-group
                id="input-group-nombre"
                label="Nombre Producto:"
                label-for="nombreProducto">
                <b-form-input
                  id="nombreProducto"
                  v-model="nombreProducto"
                  type="text"
                  required
                  placeholder="Nombre">
                </b-form-input>
              </b-form-group>
            </div>
            <div class="col-6 col-md-3">
              <b-form-group
                id="input-group-precio"
                label="Precio:"
                label-for="precioProducto">
                <b-form-input
                  id="precioProducto"
                  v-model="precioProducto"
                  type="text"
                  required
                  placeholder="0.0">
                </b-form-input>
              </b-form-group>
            </div>
          </div>

          <div class="col-12 col-sm-9 d-flex justify-content-end ">
            
          </div>
          <div class="col-12 col-sm-3 d-flex justify-content-end">
            <b-button class="mx-2 my-2 my-sm-2" variant="danger" @click="$bvModal.hide('Agregar')">Cerrar</b-button>
            <b-button class="mx-2 my-2 my-sm-2 p-0 py-2" block type="submit" variant="primary">Confirmar</b-button>
          </div>
        </b-form>
      </b-modal>
    </div>
    <!--Modal Registro Entrada Producto-->
    <div>
      <b-modal
      no-enforce-focus
      no-close-on-backdrop 
      no-close-on-esc 
      scrollable 
      hide-footer 
      size="xl" 
      centered 
      ignore-enforce-focus-selector=".swal2-popup" 
      id="entradaProducto" 
      title="Entrada Producto"
      >
        <b-form @submit="onSumitEntrada">

          <div class="row">
            <div class="col-6 col-md-3">
              <b-form-group
                id="input-group-nombre"
                label="Clave Producto:"
                label-for="claveProducto">
                <b-form-input
                  id="claveProducto"
                  v-model="productoSeleccionado.idProducto"
                  type="text"
                  required
                  placeholder=""
                  readonly
                  >
                </b-form-input>
              </b-form-group>
            </div>
            <div class="col-6 col-md-3">
              <b-form-group
                id="input-group-nombre"
                label="Nombre Producto:"
                label-for="nombreProducto">
                <b-form-input
                  id="nombreProducto"
                  v-model="productoSeleccionado.nombre"
                  type="text"
                  required
                  placeholder=""
                  readonly>
                </b-form-input>
              </b-form-group>
            </div>
            <div class="col-6 col-md-3">
              <b-form-group
                id="input-group-precio"
                label="Cantidad:"
                label-for="cantidadProducto">
                <b-form-input
                  id="cantidadProducto"
                  v-model="productoSeleccionado.cantidad"
                  type="text"
                  required
                  placeholder="0.0">
                </b-form-input>
              </b-form-group>
            </div>
            <div class="col-6 col-md-3">
              <b-form-group
                id="input-group-precio"
                label="Precio:"
                label-for="precioProducto">
                <b-form-input
                  id="precioProducto"
                  v-model="productoSeleccionado.precio"
                  type="text"
                  required
                  placeholder="0.0">
                </b-form-input>
              </b-form-group>
            </div>
          </div>

          <div class="col-12 col-sm-9 d-flex justify-content-end ">
            
          </div>
          <div class="col-12 col-sm-3 d-flex justify-content-end">
            <b-button class="mx-2 my-2 my-sm-2" variant="danger" @click="$bvModal.hide('entradaProducto')">Cerrar</b-button>
            <b-button class="mx-2 my-2 my-sm-2 p-0 py-2" block type="submit" variant="primary">Confirmar</b-button>
          </div>
        </b-form>
      </b-modal>
      
    </div>

    <!--Modal Estatus Producto-->
    <div>
      <b-modal
      no-enforce-focus
      no-close-on-backdrop 
      no-close-on-esc 
      scrollable 
      hide-footer 
      size="xl" 
      centered 
      ignore-enforce-focus-selector=".swal2-popup" 
      id="estatusProducto" 
      title="Estatus Producto"
      >
        <b-form @submit="onSumitEstatus">

          <div class="row">
            <div class="col-6 col-md-3">
              <b-form-group
                id="input-group-nombre"
                label="Clave Producto:"
                label-for="claveProducto">
                <b-form-input
                  id="claveProducto"
                  v-model="productoSeleccionadoEst.idProducto"
                  type="text"
                  required
                  placeholder=""
                  readonly
                  >
                </b-form-input>
              </b-form-group>
            </div>
            <div class="col-6 col-md-3">
              <b-form-group
                id="input-group-nombre"
                label="Nombre Producto:"
                label-for="nombreProducto">
                <b-form-input
                  id="nombreProducto"
                  v-model="productoSeleccionadoEst.nombre"
                  type="text"
                  required
                  placeholder=""
                  readonly>
                </b-form-input>
              </b-form-group>
            </div>
            <b-form-group label="Estatus">
              <b-form-select
                v-model="productoSeleccionadoEst.estatus"
                :options="[
                  { value: 1, text: 'Activo' },
                  { value: 0, text: 'Inactivo' }
                ]"
                required
              ></b-form-select>
            </b-form-group>
          </div>

          <div class="col-12 col-sm-9 d-flex justify-content-end ">
            
          </div>
          <div class="col-12 col-sm-3 d-flex justify-content-end">
            <b-button class="mx-2 my-2 my-sm-2" variant="danger" @click="$bvModal.hide('estatusProducto')">Cerrar</b-button>
            <b-button class="mx-2 my-2 my-sm-2 p-0 py-2" block type="submit" variant="primary">Confirmar</b-button>
          </div>
        </b-form>
      </b-modal>
      
    </div>

  </div>
</template>

<script>
import { PaperTable } from "@/components";
import inventarioApi from "../api/inventarioRequest"
import Vue from 'vue';
import BootstrapVue from 'bootstrap-vue';
import Swal from 'sweetalert2'

export default {
  components: {
   
  },
  data() {
    return {
      Productos: {
        items: [ ],
        fields: [
        { key: 'idProducto', label: 'Clave', sortable: true},
        { key: 'nombre', label: 'Producto'},
        { key: 'precio', label: 'Precio'},
        { key: 'stock', label: 'Stock'},
        { key: 'estatus', label: 'Estatus'},
        { key: 'actions', label: '', 'class': 'text-center'},
        { key: 'actions2', label: '', 'class': 'text-center'}
        ]
      },
      showModalEdit: false,
      nombreProducto : '',
      precioProducto: 0,
      productoSeleccionado:{ },
      productoSeleccionadoEst:{ },
      esAdmin: false,
    };
  },
  async mounted(){
    const rol = localStorage.getItem('rol'); 
    this.esAdmin = +rol === 1;
    this.Productos.items = [];
    let productos = await inventarioApi.getInventario(this.$serverIP);
    if(productos != null){
      this.Productos.items = productos.response;
      //console.log(productos.response)
    }
  },
  methods:{
    async onSumit(evt){
      evt.preventDefault();
    },
    async onSubmitRegistro(evt){
      evt.preventDefault();
        if(this.precioProducto == null || this.nombreProducto == null || this.precioProducto == 0 || this.nombreProducto.trim() == "")
        {
          Swal.fire({
            title: 'Cuidado',
            text: "Revise que los campos sean correctos y vuelva a intentarlo",
            icon: 'warning',
            showCancelButton: false,
            confirmButtonColor: '#3085d6',
            confirmButtonText: 'Ok'
          })
          return null;
        }else{
          let objProducto = {
              nombre: this.nombreProducto,
              precio: this.precioProducto,
              stock: 0,
              estatus: 1,
          }
          var insertProducto = inventarioApi.insertInventario(this.$serverIP, objProducto)
          if(insertProducto != null ){
            Swal.fire({
              icon: 'success',
              title: 'Entrada Producto',
              confirmButtonText: 'Ok'
            }).then(() => {
              this.close();         
              this.refreshTable();  
            });
          }
        }
    },
    onResetRegistro(evt){
      evt.preventDefault();
      this.$bvModal.hide('Agregar');
      this.precioProducto = 0;
      this.nombreProducto = '';
      // Reset our form values
    },
    async refreshTable(){
      this.Productos.items = [];
      let productos = await inventarioApi.getInventario(this.$serverIP);
      if(productos != null){
        this.Productos.items = productos.response;
      }
    },
    async close(){
      this.$bvModal.hide('Agregar');
      this.$bvModal.hide('entradaProducto');
      this.$bvModal.hide('estatusProducto');
      this.precioProducto = 0;
      this.nombreProducto = '';
      this.productoSeleccionado = {};
      this.productoSeleccionadoEst = {};
    },
    abrirModalEntrada(producto) {
      this.productoSeleccionado = producto;
      this.$bvModal.show('entradaProducto');
    },
    abrirModalEstatus(producto) {
      this.productoSeleccionadoEst = producto;
      this.$bvModal.show('estatusProducto');
    },
    async onSumitEntrada(evt){
      evt.preventDefault();
      if (this.productoSeleccionado.cantidad < 0 || this.productoSeleccionado.precio < 0) {
        Swal.fire({
          icon: 'warning',
          title: 'Cantidad inválida',
          text: 'No puedes ingresar una cantidad negativa.'
        });
        return;
      }else{

        if(this.productoSeleccionado.cantidad == null || this.productoSeleccionado.precio == null || this.productoSeleccionado.cantidad == 0 || this.productoSeleccionado.precio == 0)
        {
            Swal.fire({
              title: 'Cuidado',
              text: "Revise que los campos sean correctos y vuelva a intentarlo",
              icon: 'warning',
              showCancelButton: false,
              confirmButtonColor: '#3085d6',
              confirmButtonText: 'Ok'
            })
            return null;
        }else{
            let objEntrada = {
                idProducto: this.productoSeleccionado.idProducto,
                precio: this.productoSeleccionado.precio,
                stock: this.productoSeleccionado.cantidad,
                idUsuario: localStorage.idUsuario,
            }
            var insertEntrada = inventarioApi.insertEntrada(this.$serverIP, objEntrada)
            if(insertEntrada != null ){
              Swal.fire({
                icon: 'success', 
                title: 'Entrada Producto',
                confirmButtonText: 'Ok'
              }).then(() => {
                this.close();         
                this.refreshTable();  
              });
            }
        }
      }
    },
    async onSumitEstatus(evt){
      evt.preventDefault();
      if(this.productoSeleccionadoEst.estatus == null)
        {
          Swal.fire({
            title: 'Cuidado',
            text: "Revise que los campos sean correctos y vuelva a intentarlo",
            icon: 'warning',
            showCancelButton: false,
            confirmButtonColor: '#3085d6',
            confirmButtonText: 'Ok'
          })
          return null;
        }else{
          let objEstatus = {
              idProducto: this.productoSeleccionadoEst.idProducto,
              estatus: this.productoSeleccionadoEst.estatus
          }
          var insertEntrada = inventarioApi.updateEstatus(this.$serverIP, objEstatus)
          if(insertEntrada != null ){
            Swal.fire({
              icon: 'success', 
              title: 'Estatus Actualizado',
              confirmButtonText: 'Ok'
            }).then(() => {
              this.close();         
              this.refreshTable();  
            });
          }
        }
    }
  }
};
</script>
