const axios = require('axios');

var salidaApi = {
    getSalida(serverIP)  {
        return axios.get(serverIP + '/api/Salida/Lista',
        {
            headers: {
                'Authorization': `Bearer ${localStorage.token}`
            }
        }
        ).then(function (response) {
            console.log('Salida', response);
            return response.data;
        })
        .catch(function (error) {
            console.log('Salida', error);
            //request.manageError(error);
            return null;
        });
    },
    salidaProducto(serverIP, objSalida){
        return axios({
            method: 'post',
            url: serverIP + '/api/Salida/SalidaProducto',
            data: objSalida, 
            headers: {
                'Access-Control-Allow-Origin': '*',
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${localStorage.token}`
            }
        })
        .then(function (response) {
            return response.data;
        })
        .catch(function (error) {
            request.manageError(error);
            return null;
        });
    },
}
export default salidaApi;