<template>
  <div class="container">
     <div class="">
        <div class="row mb-3">
          <div class="col-md-4">
            <b-form-group label="Filtrar por tipo de movimiento">
              <b-form-select v-model="filtroTipo" :options="[
                { value: '', text: 'Todos' },
                { value: 'entrada', text: 'Entrada' },
                { value: 'salida', text: 'Salida' }
              ]"></b-form-select>
            </b-form-group>
          </div>
        </div>
      </div>
    <b-table
      responsive
      striped
      hover
      :items="movimientosFiltrados"
      :fields="Movimientos.fields"
      head-variant="dark"
    >
    </b-table>
  </div>
</template>

<script>
import { PaperTable } from "@/components";
import historicoApi from "../api/historicoRequest"
import Vue from 'vue';
import BootstrapVue from 'bootstrap-vue';
import Swal from 'sweetalert2'

export default {
  components: {
   
  },
  data() {
    return {
      filtroTipo: '',
      Movimientos: {
        items: [ ],
        fields: [
        { key: 'idProducto', label: 'Clave', sortable: true},
        { key: 'nombre', label: 'Producto'},
        { key: 'cantidad', label: 'Cantidad'},
        { key: 'usuario', label: 'Usuario'},
        { key: 'tipo', label: 'Movimiento'},
        { key: 'fecha', label: 'Fecha'},
        ]
      },
      showModalEdit: false,
      nombreProducto : '',
      precioProducto: 0,
      productoSeleccionado:{ },
      productoSeleccionadoEst:{ },
      esAdmin: false,
    };
  },
  async mounted(){
    const rol = localStorage.getItem('rol'); 
    this.esAdmin = +rol === 1;
    this.Movimientos.items = [];
    let movimientos = await historicoApi.getHistorico(this.$serverIP);
    if(movimientos != null){
      this.Movimientos.items = movimientos.response;
    }
  },
  methods:{
    async onSumit(evt){
      evt.preventDefault();
    },
    async refreshTable(){
      this.Movimientos.items = [];
      let movimientos = await historicoApi.getHistorico(this.$serverIP);
      if(movimientos != null){
        this.Movimientos.items = movimientos.response;
      }
    },
  },
  computed: {

    movimientosFiltrados() {
      if (!this.filtroTipo) {
        return this.Movimientos.items;
      }
      return this.Movimientos.items.filter(m => 
        m.tipo.toLowerCase() === this.filtroTipo.toLowerCase()
      );
    }
  }
};
</script>
