    },
    logOut(){
      localStorage.removeItem('token');
      localStorage.removeItem('idUsuario');
      localStorage.removeItem('mail');
      localStorage.removeItem('name');
      localStorage.removeItem('rol');
      router.push('/');
    }
  }
