<template>
  <body class="login">
    <div class="wrapper fadeInDown">
        <div id="formContent">
            <!-- Tabs Titles -->

            <!-- Icon -->
            <div class="fadeIn first">
                <br>
                <br>
                <!--<img src="@/assets/img/logo-azul.png" id="icon" alt="User Icon" />
            -->
            </div>
            <br>
            <!-- Login Form -->
            <form v-on:submit.prevent="login">
                <input type="text" id="email" class="fadeIn second" name="email" placeholder="Usuario" v-model="email">
                <input type="password" id="password" class="fadeIn third" name="password" placeholder="Contraseña" v-model="password">
                <input type="submit" class="fadeIn fourth" value="Iniciar Sesión">
            </form>

            <!-- Remind Passowrd -->   
            <div class="alert alert-danger" role="alert" v-if="error">
                {{error_msg}}
            </div>  
        </div>
        
    </div>

  </body>
</template>

<script>
import axios from 'axios';

import router from '../router/index';
import userApi from "../api/userRequest"

export default {
    name: 'Login',
    comments:{

    },
    data: function(){
        return{
            email: "",
            password: "",
            error: false,
            error_msg: "",
            route: ""
        }
    },
    methods:{
        async login(){
            if(this.email == "" || this.password == ""){
                this.error = true;
                if(this.email == "" && this.password == ""){
                    this.error_msg = "Por favor, llene todos los campos correspondientes";
                }
                else if(this.email == ""){
                    this.error_msg = "Por favor, Ingresa tu Correo";
                }
                else if(this.password == ""){
                    this.error_msg = "Por favor, Ingresa tu Contraseña";
                }
            }
            else{
                let user = await userApi.login(this.$serverIP, this.email, this.password);
                if(user != null){
                    this.error = false;
                    if (localStorage.rol == 1){
                        router.push('app/Inventario')
                    } else {
                        router.push('app/Inventario')
                    }
                } else {
                    this.error = true;
                    this.error_msg = "Verifica tu Correo / Contraseña";
                }
            }
        }
    }
}
</script>

<style scoped>
    /* BASIC */
    
    html {
        background-color: #56baed;
    }
    body {
        background-position: center center;
        background-repeat: no-repeat;
        background-attachment: fixed;
        background-size: cover;
        font-family: "Poppins", sans-serif;
        height: 100vh;
    }

    a {
        color: #92badd;
        display:inline-block;
        text-decoration: none;
        font-weight: 400;
    }

    h2 {
        text-align: center;
        font-size: 16px;
        font-weight: 600;
        text-transform: uppercase;
        display:inline-block;
        margin: 40px 8px 10px 8px; 
        color: #cccccc;
    }



    /* STRUCTURE */

    .wrapper {
        display: flex;
        align-items: center;
        flex-direction: column; 
        justify-content: center;
        width: 100%;
        min-height: 100vh;
        padding: 20px;
        background-image: url("../assets/img/f2.jpg");
        background-position: center center;
        background-repeat: no-repeat;
        background-attachment: fixed;
        background-size: cover;
    }

    #formContent {
        -webkit-border-radius: 10px 10px 10px 10px;
        border-radius: 10px 10px 10px 10px;
        background: #fff;
        padding: 30px;
        width: 90%;
        max-width: 450px;
        position: relative;
        padding: 0px;
        -webkit-box-shadow: 0 30px 60px 0 rgba(0,0,0,0.3);
        box-shadow: 0 30px 60px 0 rgba(0,0,0,0.3);
        text-align: center;
    }

    #formFooter {
        background-color: #f6f6f6;
        border-top: 1px solid #dce8f1;
        padding: 25px;
        text-align: center;
        -webkit-border-radius: 0 0 10px 10px;
        border-radius: 0 0 10px 10px;
    }



    /* TABS */

    h2.inactive {
        color: #cccccc;
    }

    h2.active {
        color: #0d0d0d;
        border-bottom: 2px solid #5fbae9;
    }



    /* FORM TYPOGRAPHY*/

    input[type=button], input[type=submit], input[type=reset]  {
        background-color: #56baed;
        border: none;
        color: white;
        padding: 15px 80px;
        text-align: center;
        text-decoration: none;
        display: inline-block;
        text-transform: uppercase;
        font-size: 13px;
        -webkit-box-shadow: 0 10px 30px 0 rgba(95,186,233,0.4);
        box-shadow: 0 10px 30px 0 rgba(95,186,233,0.4);
        -webkit-border-radius: 5px 5px 5px 5px;
        border-radius: 5px 5px 5px 5px;
        margin: 5px 20px 40px 20px;
        -webkit-transition: all 0.3s ease-in-out;
        -moz-transition: all 0.3s ease-in-out;
        -ms-transition: all 0.3s ease-in-out;
        -o-transition: all 0.3s ease-in-out;
        transition: all 0.3s ease-in-out;
    }

    input[type=button]:hover, input[type=submit]:hover, input[type=reset]:hover  {
        background-color: #39ace7;
    }

    input[type=button]:active, input[type=submit]:active, input[type=reset]:active  {
        -moz-transform: scale(0.95);
        -webkit-transform: scale(0.95);
        -o-transform: scale(0.95);
        -ms-transform: scale(0.95);
        transform: scale(0.95);
    }

    input[type=text] {
        background-color: #f6f6f6;
        border: none;
        color: #0d0d0d;
        padding: 15px 32px;
        text-align: center;
        text-decoration: none;
        display: inline-block;
        font-size: 16px;
