<template>
  <div class="container">
    <b-table 
    responsive
    striped
    hover
    :items="Productos.items" 
    :fields="Productos.fields"
    head-variant="dark"
    >
    <template #cell(estatus)="data">
      <span :class="{'text-success': data.item.estatus === 1, 'text-danger': data.item.estatus === 0}">
        {{ data.item.estatus === 1 ? 'Activo' : 'Inactivo' }}
      </span>
    </template>
    <template v-slot:cell(actions)="data">
      <a v-b-tooltip.hover title="Salida Producto" @click="abrirModalSalida(data.item)">
        <b-icon icon="box-arrow-right" font-scale = "1.5"></b-icon>
      </a>
    </template>
    </b-table>

    <!--Modal Registro Entrada Producto-->
    <div>
      <b-modal
      no-enforce-focus
      no-close-on-backdrop 
      no-close-on-esc 
      scrollable 
      hide-footer 
      size="xl" 
      centered 
      ignore-enforce-focus-selector=".swal2-popup" 
      id="salidaProducto" 
      title="Entrada Producto"
      >
        <b-form @submit="onSumitSalida">

          <div class="row">
            <div class="col-6 col-md-3">
              <b-form-group
                id="input-group-nombre"
                label="Clave Producto:"
                label-for="claveProducto">
                <b-form-input
                  id="claveProducto"
                  v-model="productoSeleccionado.idProducto"
                  type="text"
                  required
                  placeholder=""
                  readonly
                  >
                </b-form-input>
              </b-form-group>
            </div>
            <div class="col-6 col-md-3">
              <b-form-group
                id="input-group-nombre"
                label="Nombre Producto:"
                label-for="nombreProducto">
                <b-form-input
                  id="nombreProducto"
                  v-model="productoSeleccionado.nombre"
                  type="text"
                  required
                  placeholder=""
                  readonly>
                </b-form-input>
              </b-form-group>
            </div>
            <div class="col-6 col-md-3">
              <b-form-group
                id="input-group-precio"
                label="Cantidad:"
                label-for="cantidadProducto">
                <b-form-input
                  id="cantidadProducto"
                  v-model="productoSeleccionado.cantidad"
                  type="text"
                  required
                  placeholder="0.0">
                </b-form-input>
              </b-form-group>
            </div>
            <div class="col-6 col-md-3">
              <b-form-group
                id="input-group-precio"
                label="Precio:"
                label-for="precioProducto">
                <b-form-input
                  id="precioProducto"
                  v-model="productoSeleccionado.precio"
                  type="text"
                  required
                  readonly
                  placeholder="0.0">
                </b-form-input>
              </b-form-group>
            </div>
          </div>

          <div class="col-12 col-sm-9 d-flex justify-content-end ">
            
          </div>
          <div class="col-12 col-sm-3 d-flex justify-content-end">
            <b-button class="mx-2 my-2 my-sm-2" variant="danger" @click="$bvModal.hide('salidaProducto')">Cerrar</b-button>
            <b-button class="mx-2 my-2 my-sm-2 p-0 py-2" block type="submit" variant="primary">Confirmar</b-button>
          </div>
        </b-form>
      </b-modal>
    </div>
  </div>
</template>

<script>
import { PaperTable } from "@/components";
import salidaApi from "../api/salidaRequest"
import Vue from 'vue';
import BootstrapVue from 'bootstrap-vue';
import Swal from 'sweetalert2'

export default {
  components: {
   
  },
  data() {
    return {
      Productos: {
        items: [ ],
        fields: [
        { key: 'idProducto', label: 'Clave', sortable: true},
        { key: 'nombre', label: 'Producto'},
        { key: 'precio', label: 'Precio'},
        { key: 'stock', label: 'Stock'},
        { key: 'estatus', label: 'Estatus'},
        { key: 'actions', label: '', 'class': 'text-center'},
        ]
      },
      showModalEdit: false,
      nombreProducto : '',
      precioProducto: 0,
      productoSeleccionado:{},
      esAdmin: false,
    };
  },
  async mounted(){
    const rol = localStorage.getItem('rol'); 
    this.esAdmin = +rol === 1;
    this.Productos.items = [];
    let productos = await salidaApi.getSalida(this.$serverIP);
    if(productos != null){
      this.Productos.items = productos.response;
      //console.log(productos.response)
    }
  },
  methods:{
    async onSumit(evt){
      evt.preventDefault();
    },
    async refreshTable(){
      this.Productos.items = [];
      let productos = await salidaApi.getSalida(this.$serverIP);
      if(productos != null){
        this.Productos.items = productos.response;
      }
    },
    async close(){
      this.$bvModal.hide('salidaProducto');
      this.precioProducto = 0;
      this.nombreProducto = '';
      this.productoSeleccionado = {};
    },
    abrirModalSalida(producto) {
      this.productoSeleccionado = producto;
      this.$bvModal.show('salidaProducto');
    },
    async onSumitSalida(evt){
      evt.preventDefault();
      if (this.productoSeleccionado.cantidad < 0 ) {
        Swal.fire({
          icon: 'warning',
          title: 'Cantidad inválida',
          text: 'No puedes ingresar una cantidad negativa.'
        });
        return;
      }else{

        if(this.productoSeleccionado.cantidad == null || this.productoSeleccionado.precio == null || this.productoSeleccionado.cantidad == 0 || this.productoSeleccionado.precio == 0)
        {
            Swal.fire({
              title: 'Cuidado',
              text: "Revise que los campos sean correctos y vuelva a intentarlo",
              icon: 'warning',
              showCancelButton: false,
              confirmButtonColor: '#3085d6',
              confirmButtonText: 'Ok'
            })
            return null;
        }else{
            let objSalida = {
                idProducto: this.productoSeleccionado.idProducto,
                stock: this.productoSeleccionado.cantidad,
                idUsuario: localStorage.idUsuario,
            }
            var insertEntrada = salidaApi.salidaProducto(this.$serverIP, objSalida)
            if(insertEntrada != null ){
              Swal.fire({
                icon: 'success', 
                title: 'Salida Producto',
                confirmButtonText: 'Ok'
              }).then(() => {
                this.close();         
                this.refreshTable();  
              });
            }
        }
      }
    },
  }
};
</script>
