const axios = require('axios');

var historicoApi = {
    getHistorico(serverIP)  {
        return axios.get(serverIP + '/api/Historico/Lista',
        {
            headers: {
                'Authorization': `Bearer ${localStorage.token}`
            }
        }
        ).then(function (response) {
            console.log('Historico', response);
            return response.data;
        })
        .catch(function (error) {
            console.log('Historico', error);
            //request.manageError(error);
            return null;
        });
    },
    
}
export default historicoApi;