const axios = require('axios');
var userApi = {
    login(serverIP, email, password)  {
        return axios.post(serverIP + '/api/Login/Validar?email=' + encodeURIComponent(email) + '&password=' + encodeURIComponent(password))
        .then(function (response) {
            console.log('LOGIN', response);
            localStorage.token = response.data.token.token;
            localStorage.idUsuario = parseInt(response.data.token.idUsuario);
            localStorage.mail = response.data.token.email;
            localStorage.name = response.data.token.nombre;
            localStorage.rol = response.data.token.idRol;
            return response.data;
        })
        .catch(function (error) {
            console.log('LOGIN', error);
            //request.manageError(error);
            return null;
        });
    },
}
export default userApi;